		<doc name="Difference_between_jQuery">
			<h2>Difference between jQuery</h2>
			<div class="cPreDesc">
				sQuery is not a simple jQuery clone. It's made for using with modern frontend frameworks.
				There are some differences between jQuery.

				<div style="margin-top: 10px">
					<div class="cAccr">
						<h3>sQuery uses sq not $. But you can customize!</h3>
						<p>
							sQuery avoids using $ since Svelte and jQuery use $.<br>
							Actually, you <b>can</b> use $ in sQuery! See <a href="./?n=Use_with_jQuery#/docs" target=_blank>Use like jQuery</a>
						</p>
					</div>
					
					<div class="cAccr">
						<h3>sQuery doesn't have $.ajax function. Use fetch or axios instead</h3>
						<p>
							Since this is the modern trend, sQuery also doesn't have any ajax functions just like <b>React/Vue/Svelte</b> do.<br>
							You should use <a href="https://github.com/axios/axios" target=_blank>axios</a> or <a href="https://www.javascripttutorial.net/javascript-fetch-api/" target=_blank>native fetch function</a> not $.ajax.
						</p>
					</div>

					<div class="cAccr">
						<h3>Never use :contains / :is / :visible</h3>
						<p>
							:contains in jQuery used to be very widely used. However they have been depricated in the latest jQuery.
							<br><br>
							<b>$(":contains('selector')")</b><br>
							<br>
							sQuery also doesn't support them for various performance reasons. Instead, sQuery just supports performant methods to do the same things just like the latest jQuery does.<br>
							<br>
							<b>
								.contains('selector')<br>
								.is('selector')<br>
								.isVisible('selector')
							</b>
						</p>
					</div>

					<div class="cAccr">
						<h3>Why sQuery doesn't support all the functions in jQuery?</h3>
						<p>
							sQuery has major core functions in jQuery.<br>
							However, it's not made for supporting everything.<br>
							sQuery is made for working with modern js frameworks such as <b>React/Vue/Svelte/Angular</b> with replacing lengthy vanilla JavaScript codes.<br>
							<br>
							As a result, with sQuery, the total code size could be smaller than without it especially in big projects.<br>
							If sQuery supports all functions and all features in jQuery, the code size would be 3 times or something? It wouldn't be worthy at all!<br>
							That's why I cut down all the unessential functions from jQuery since the purpose of sQuery is simple, solid, and small.<br>
							<br>
							For instance, let's assume the following scenario: sQuery + Vue.js:<br>
							Thanks to sQuery, you can code just like jQuery, and you can also use the virtual DOM and auto code optimizations Vue provides.<br>
							If you do DOM manipulations relatively a lot, sQuery doesn't increase the total code size as a result of cutting down of lengthy vanila JS codes, so you won't lose anything in this scenario.
						</p>
					</div>

				</div>
			</div>
		</doc>

